<template>
  <!-- 数字商品 -->
  <div class="page">
    <user-info class="user-wrap"></user-info>
    <div class="bg">
      <img src="~@/assets/images/collection/bg_list@2x.png" alt="" />
    </div>

    <div class="cont">
      <!-- <div class="top">
        <img src="~@/assets/images/collection/shop_logo@2x.png" />
      </div> -->
      <div class="top-title">
        <img src="~@/assets/images/collection/title@2x.png" />
      </div>
      <div class="list">
        <div class="shop-wrap">
          <div class="shopi" v-for="(item, index) in shopList" :key="index">
            <div class="shop-img">
              <img :src="item.pic_url" />
            </div>
            <div class="shop-info">
              <div class="shop-tit">{{ item.name }}</div>
              <div class="shop-count">库存:{{ item.avaiable_num }}</div>
              <div class="shop-price" @click="buyShop(item)">
                <img src="~@/assets/images/collection/star_big@2x.png" />
                {{ item.price }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Modal class="modal" :visible.sync="isBuyShow" title="数字藏品详情">
      <div class="modal-content" slot="content">
        <div class="shop-logo">
          <img :src="goods.pic_url" />
          <div class="tit">{{ goods.name }}</div>
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <div class="footer-wrap" @click="doBuy">
          <img src="@/assets/images/common/btn_buy@2x.png" />
          <div class="buy-price">{{ goods.price }}星钻</div>
        </div>
      </div>
    </Modal>

    <!-- 星钻不足 -->
    <Modal class="modal" :visible.sync="isModalNotenough" :title="modalTitle">
      <div class="modal-content" slot="content">
        <div class="text mb-24">{{ contentText }}</div>
        <div v-if="contentSubText" class="sub-text mb-32">
          {{ contentSubText }}
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          v-if="buySuccess"
          @click="cancel2"
          src="@/assets/images/common/modal_btn_zdl.png"
          alt=""
        />
        <img
          v-else
          @click="cancel3"
          src="@/assets/images/common/modal_btn_earn.png"
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import UserInfo from '@/components/userInfo'
import Modal from '@/components/commonModal'

export default {
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      shopList: [
        {
          name: '小雪-信小福',
          // src: './static/img/collection/shop1@2x.png',
          avaiable_num: 56456,
          create_time: '2022-02-28 17:10:56',
          id: 16,
          pic_url: null,
          price: 456
        }
      ],
      isBuyShow: false, // 购买
      goods: {
        pic_url: '',
        id: '',
        price: '',
        avaiable_num: '',
        name: ''
      }, // 当前购买商品信息
      modalTitle: '提示', // 弹窗顶部文字
      contentText: '', // 弹窗内容
      contentSubText: '', // 弹窗副内容
      isModalNotenough: false, // 星钻不足
      isModalSucess: false, // 购买成功
      buySuccess: false
    }
  },
  components: {
    UserInfo,
    Modal
  },
  created () {
    this.getDigitalList()
  },
  methods: {
    buyShop (item) {
      this.goods = item
      this.isBuyShow = true
    },
    async doBuy () {
      if (this.userInfo.available < this.goods.price) {
        this.isModalNotenough = true
        this.isBuyShow = false
        this.buySuccess = false
        this.modalTitle = '购买失败'
        this.contentText = '星钻不足'
        this.contentSubText = ''
        return false
      }
      this.isBuyShow = false
      this.Indicator.open({
        text: '购买中...',
        spinnerType: 'fading-circle'
      })
      await this.$request('get', 'user/buydigitalcollection', {
        uid: this.userInfo.uid,
        cid: this.goods.id
      }, { noErrorMsg: true }).then(res => {
        this.Indicator.close()
        this.buySuccess = true
        this.modalTitle = '购买成功'
        this.contentText = '可在我的藏品中查看'
        this.contentSubText = ''
      }).catch(
        err => {
          this.Indicator.close()
          this.buySuccess = false
          if (err.error_no === '700003') {
            this.modalTitle = '购买失败'
            this.contentText = '星钻不足'
            this.contentSubText = ''
          } else {
            this.modalTitle = '消费失败'
            this.contentText = err.error_info
            this.contentSubText = ''
          }
        }
      )
      this.isModalNotenough = true
    },
    async getDigitalList () {
      let res = await this.$request('get', 'activity/getcollectionlist', { type: this.$route.params.type }).catch(
        err => {
          // this.buySuccess = false
          if (err.error_no === '700003') {
            this.modalTitle = '消费失败'
            this.contentText = '星钻不足'
            this.contentSubText = ''
          } else {
            this.modalTitle = '消费失败'
            this.contentText = err.error_info
            this.contentSubText = ''
          }
        }
      )
      if (res && res.error_no === '0') {
        // this.rule = res.data
        console.log(res.data)
        this.shopList = res.data
        this.shopList.forEach((item, index) => {
          item.pic_url = './static/img/collection/shop1@2x.png'
          this.shopList[index] = item
        })
        // this.isShowNotice = true
      }
    },
    cancel2 () {
      this.isModalNotenough = false
    },
    cancel3 () {
      this.isModalNotenough = false
      this.$router.push({ name: 'home', params: { earnStar: '1' } })
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  background-color: #ffe1c5;
  min-height: 100vh;
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    img {
      width: 100%;
    }
  }
  .user-wrap {
    position: relative;
    left: 0;
    top: 0;
    z-index: 2;
  }
  .cont {
    width: 100%;
    min-height: 100%;
    position: absolute;
    top: 0;
    // left: 0;
    .top {
      width: 100%;
      height: 620px;

      .flex_func(row,center,flex-end,nowrap);
      > img {
        width: 381px;
        height: 437px;
      }
    }
    .top-title {
      width: 100%;
      height: 50px;
      margin-top: 390px;
      .flex_func(column, center,center,nowrap);
      > img {
        width: 320px;
        height: 27px;
      }
    }
    .list {
      width: 100%;
      // min-height: calc(100% - 620px);
      background-color: #ffe1c5;
      padding: 0 40px;
      margin-top: 10px;
      .shop-wrap {
        // margin-top: -50px;
        .flex_func(column, flex-satrt,center,wrap);
        .shopi {
          width: 680px;
          height: 570px;
          padding: 0 7px;
          margin-bottom: 65px;
          .bg-image("~@/assets/images/collection/item_bg","png");
          .shop-img {
            width: 100%;
            // height: 205px;
            > img {
              width: 100%;
              height: 100%;
            }
          }
          .shop-info {
            width: 100%;
            height: 110px;
            padding: 0 30px;
            text-align: center;
            .flex_func(row, space-between,center,wrap);
            .shop-tit {
              width: 100%;
              height: 24px;
              font-size: 34px;
              font-weight: 500;
              color: #582b0b;
              line-height: 50px;
              text-align: left;
            }
            .shop-count {
              font-size: 22px;
              color: #c08a5e;
              line-height: 20px;
              height: 20px;
            }
            .shop-price {
              font-size: 40px;
              color: #eb584c;
              > img {
                width: 40px;
                height: 35px;
              }
            }
          }
        }
        .shopi-center {
          margin: 0 88px;
        }
      }
    }
  }
}
.modal {
  .shop-logo {
    > img {
      width: 210px;
      height: 210px;
    }
    .tit {
      font-size: 32px;
      font-weight: 600;
      color: #333333;
      width: 100%;
      height: 50px;
      line-height: 50px;
      margin-top: 10px;
    }
  }
  .modal-footer {
    position: relative;
    .flex_func(column,center,center,nowrap);
    .footer-wrap {
      width: 350px;
      height: 128px;
      .flex_func(column,center,center,nowrap);
      .buy-price {
        font-size: 20px;
        color: #ffe29d;
        position: absolute;
        bottom: calc(50% - 25px);
      }
    }
  }
}
</style>



